.header {
  background: green;
  display: table;
  width: 100%
}

.item {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: table-cell;
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
  margin: 5px;
  /* 원하는 마진 값으로 조절 */
}

/* 또는 개별적으로 상하좌우 마진을 설정할 수도 있습니다. */

.item {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: table-cell;
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
  margin: 5px;
  /* 원하는 마진 값으로 조절 */
  margin-top: 5px;
  margin-right: 10px;
  margin-bottom: 5px;
  margin-left: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* Example box shadow */
}

.item:hover {
  background: orange;
  box-shadow: 0 8px 16px rgba(15, 15, 15, 30);
  /* Box shadow on hover */
}